@media (min-width: 640px) {
  .sm\:rounded-t-lg {
    border-top-left-radius: 0.5rem;
    border-top-right-radius: 0.5rem;
  }

  .sm\:inline-flex {
    display: -webkit-inline-flex;
    display: inline-flex;
  }

  .sm\:h-14 {
    height: 3.5rem;
  }

  .sm\:h-36 {
    height: 9rem;
  }

  .sm\:text-sm {
    font-size: 0.875rem;
    line-height: 1.25rem;
  }

  .sm\:text-2xl {
    font-size: 1.5rem;
    line-height: 2rem;
  }

  .sm\:overflow-x-visible {
    overflow-x: visible;
  }

  .sm\:px-10 {
    padding-left: 2.5rem;
    padding-right: 2.5rem;
  }

  .sm\:right-2 {
    right: 0.5rem;
  }

  .sm\:w-20 {
    width: 5rem;
  }

  .sm\:w-72 {
    width: 18rem;
  }

  .sm\:w-max {
    width: -webkit-max-content;
    width: max-content;
  }

  .sm\:grid-cols-7 {
    grid-template-columns: repeat(7, minmax(0, 1fr));
  }

  .sm\:grid-cols-11 {
    grid-template-columns: repeat(11, minmax(0, 1fr));
  }

  .sm\:col-span-10 {
    grid-column: span 10 / span 10;
  }
}

